<template>
  <vs-table striped class=" mt-5">
    <template #header>

      <div class=" w-full">
        <span class=" text-xs">Modalidades</span>
        <v-select
            v-model="modalidadSelected"
            :options="modalidadData"
            label="nombre"
            index="id"
            @input="$emit('modalidadSelected', modalidadSelected)"
        >
        </v-select>
      </div>


      <div class=" flex mt-5">
        <span class=" text-2xl font-medium"
        >Resumen de Escuelas ({{ modalidadSelected.nombre }})</span
        >
        <div class=" rounded-lg  bg-gray-500 px-1 text-white ml-2">
          <span class=" text-lg font-medium">Total {{ get_total }}</span>
        </div>
      </div>
    </template>


    <template #thead>
      <vs-tr>
        <vs-th>
          Escuela
        </vs-th>

        <vs-th>
          Cantidad
        </vs-th>
      </vs-tr>
    </template>
    <template #tbody>
      <vs-tr :key="i" v-for="(tr, i) in dataList" :data="tr">
        <vs-td>
          {{ tr.escuela__nombre }}
        </vs-td>

        <vs-td>
          {{ tr.total }}
        </vs-td>
      </vs-tr>
    </template>

    <template #notFound>
      No existen datos 😢
    </template>
  </vs-table>
</template>

<script>
export default {
  props: {
    dataList: {
      type: Array,
    },
  },

  data() {
    return {
      modalidadData: [],
      modalidadSelected: {
        nombre: ""
      }
    };
  },

  mounted() {
    this.getModalidades()
  },

  methods: {
    async getModalidades() {
      this.$store.dispatch("modalidad/set_modalidad").then(resp => {
        this.modalidadData = resp;
        this.modalidadSelected = this.modalidadData[this.modalidadData.length - 1]
        this.$emit("modalidadSelected", this.modalidadSelected)
      })
    }
  },

  computed: {
    get_total() {
      try {
        let total = 0;
        this.dataList.forEach((element) => {
          total += parseInt(element.total);
        });
        return total;
      } catch (error) {
        return 0;
      }
    },

    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },
  },
};
</script>
