<template>
  <vs-table striped class=" mt-5">
    <template #header>


      <div class=" flex">
        <span class=" text-2xl font-medium">Resumen de Modalidades</span>
        <div class=" rounded-lg  bg-gray-500 px-1 text-white ml-2">
          <span class=" text-lg font-medium">Total {{ get_total }}</span>
        </div>
      </div>
    </template>
    <template #thead>
      <vs-tr>
        <vs-th>
          Modalidad
        </vs-th>

        <vs-th>
          Cantidad
        </vs-th>
      </vs-tr>
    </template>
    <template #tbody>
      <vs-tr :key="i" v-for="(tr, i) in dataList" :data="tr">
        <vs-td>
          {{ tr.modalidad__nombre }}
        </vs-td>

        <vs-td>
          {{ tr.total }}
        </vs-td>
      </vs-tr>
    </template>

    <template #notFound>
      No existen datos 😢
    </template>
  </vs-table>
</template>

<script>
export default {
  props: {
    dataList: {
      type: Array,
    },
  },

  computed: {
    get_total() {
      try {
        let total = 0;
        this.dataList.forEach((element) => {
          total += parseInt(element.total);
        });
        return total;
      } catch (error) {
        return 0;
      }
    },
  },
};
</script>
