<template>
  <div>
    <span class="text-3xl font-semibold">Bienvenido al Administrador de DA-UNAP</span>

    <div class="w-full" v-if="!bloquear_filial">
      <span class="text-xs">Sede/Filial</span>
      <v-select
        v-model="sedeSelected"
        :options="sede_data"
        label="nombre"
        index="id"
        @input="
  getEscuelaData();
getModalidadData();
        "
      ></v-select>
    </div>

    <div v-if="visible" class="mt-10 flex justify-between flex-col lg:flex-row">
      <tabla-modalidad :dataList="modalidadData" />
      <div class="w-20"></div>
      <tabla-ecuela
        :dataList="escuelaData"
        @modalidadSelected="(e) => {
          modalidadXEscuelasSelected = e;
          getEscuelaData()
        }"
      />
    </div>

    <div v-if="visible && showUniversidades">
      <tabla-universidades :dataList="universidadData" />
    </div>
  </div>
</template>
<script>
import TablaEcuela from "./dashboard/TablaEcuela.vue";
import TablaModalidad from "./dashboard/TablaModalidad.vue";

import axios from "@//plugins//axios";
import TablaUniversidades from "./dashboard/TablaUniversidades.vue";

export default {
  components: { TablaModalidad, TablaEcuela, TablaUniversidades },

  data() {
    return {
      visible: false,
      sede_data: [],
      bloquear_filial: false,
      sedeSelected: {},
      escuelaData: [],
      modalidadData: [],
      universidadData: [],
      modalidadXEscuelasSelected: {},
      showUniversidades: true,

    };
  },

  computed: {
    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },
  },

  async mounted() {
    setTimeout(async () => {
      this.visible =
        this.get_auth.group == "administrador" ||
        this.get_auth.group == "sede" ||
        this.get_auth.group == "consultor";

      await this.set_sede();
      this.filterFilial();
      this.getEscuelaData();
      this.getModalidadData();
      this.getSumatoriaUniversidades();
    }, 1000)

  },

  methods: {


    async getSumatoriaUniversidades() {
      try {
        const { data } = await axios.get("get_reporte_sumatoria_universidades");
        this.universidadData = data;
        this.showUniversidades = this.universidadData.length > 0
      } catch (error) {
        //
      }
    },

    getEscuelaData() {
      let sede = !this.sedeSelected.nombre
        ? ""
        : this.sedeSelected.nombre.toLowerCase();
      sede = sede == "todos" ? "" : sede;
      const modalidad = this.modalidadXEscuelasSelected.codigo || ""


      const params = {
        sede,
        modalidad
      }

      axios
        .get("get_reporte_sumatoria_ecuelas", {
          params: params,
        })
        .then((resp) => {
          const { data } = resp;
          this.escuelaData = data;
        });
    },

    getModalidadData() {
      let sede = !this.sedeSelected.nombre
        ? ""
        : this.sedeSelected.nombre.toLowerCase();
      sede = sede == "todos" ? "" : sede;

      axios
        .get("get_reporte_sumatoria_modalidades", {
          params: { sede },
        })
        .then((resp) => {
          const { data } = resp;
          this.modalidadData = data;
        });
    },

    async set_sede() {
      try {
        const { data } = await axios.get("cede");
        this.sede_data = data;

        if (this.visible) {
          this.sede_data.push({ id: 0, nombre: "TODOS" });
        }

        this.sedeSelected = this.sede_data[this.sede_data.length - 1]
      } catch (error) {
        return null;
      }
    },

    filterFilial() {
      const username = this.get_auth.username;
      const result = ["yurimaguas", "contamana", "datem", "requena"].find(
        (item) => item.trim().toLowerCase() == username.trim().toLowerCase()
      );

      if (result) {
        //
        const index = this.sede_data.findIndex(
          (item) =>
            item.nombre.trim().toLowerCase() == result.trim().toLowerCase()
        );

        if (index > -1) {
          this.sedeSelected = this.sede_data[index];
          this.bloquear_filial = index > -1;
        }
      }
    },
  },
};
</script>
